header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 5; }

.hamburger {
    display: none; }

a {
    text-decoration: none; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: white;
		font-size: 15px;
		font-weight: 300;
		text-decoration: none; }
	&_item {
        border-right: 2px dotted black;
        padding-right: 28px;
        &:last-child {
            border-right: none; } } }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		margin-top: 6px;
		color: white;
		opacity: 0.5;
		font-style: 13px;
		font-weight: 400; }
	&_call {
		margin-top: 19px;
		color: white;
		font-size: 12px;
		font-weight: 400;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		color: white;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $color_blue 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: white;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }


.promo {
	min-height: 800px;
	background: url('../img/uber_bg.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: white;
		font-size: 60px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: white;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&_descr {
		color: white;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $color_blue 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: white;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.occasion {
	min-height: 900px;
	padding-top: 71px; }

.label {
	position: relative;
	margin: 0 auto;
	// padding: 5px 12px
	width: 112px;
	height: 24px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 400;
	text-align: center;
	text-transform: uppercase;
	line-height: 24px; }

.label2 {
	background-color: #ffffff;
	color: #070716;
	z-index: 2; }

.title {
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-align: center;
	text-transform: uppercase; }

.title2 {
	position: relative;
	color: #ffffff;
	z-index: 2; }

.subtitle {
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	line-height: 21px;
	text-align: center;
	margin-top: 19px;
	z-index: 2; }


.subtitle2 {
	position: relative;
	color: #ffffff; }

.occasion {
	&_items {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }
	&_block {
		width: 343px; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $color_blue;
		border-radius: 100%; }
	&_subheader {
		color: $color_blue;
		font-size: 17px;
		font-weight: 400; }
	&_text {
		color: #202020;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px;
		text-align: justify; } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/mobile_bg.jpg') center center/cover no-repeat;
	.row {
		margin-top: 32px; }
	&_items {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 91px;
		&_1 {
			background: url('../img/App/App1.jpg') center center/cover no-repeat; }
		&_2 {
			background: url('../img/App/App2.jpg') center center/cover no-repeat; }
		&_3 {
			background: url('../img/App/App3.jpg') center center/cover no-repeat; }
		&_4 {
			background: url('../img/App/App4.jpg') center center/cover no-repeat; }
		&_5 {
			background: url('../img/App/App5.jpg') center center/cover no-repeat; }
		&_6 {
			background: url('../img/App/App6.jpg') center center/cover no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&::before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $color_blue; }
			&::after {
				content: '';
				display: block;
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				height: 14px;
				width: 2px;
				background-color: $color_blue; }
			&:hover {
				&::before, &::after {
					background-color: $color_red;
					transition: 0.3s all; } } } } }

.choice {
	background: url('../img/foundation.jpg');
	min-height: 895px;
	position: relative;
	padding: 57px 0 138px 0;
	&_img {
		display: block;
		position: relative;
		z-index: 2;
		margin: 56px auto 0 auto; }
	&_black {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 499px;
		background-color: #070716;
		z-index: 1; }

	&_descr {
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		text-align: center; } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		color: $color_blue;
		font-size: 22px;
		font-weight: 400; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			color: #222222;
			font-size: 17px;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&::before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue.png') center (center / cover) no-repeat; } } }
	&_descr {
		color: #1ca8c3;
		font-style: 22px;
		font-weight: 300; }
	.warning {
		.require_title {
			color: $color_red; }
		.require_list {
			li {
				&::before {
					content: '';
					background: url('../icons/check_red.png') center (center / cover) no-repeat; } } } } }

.world {
	min-height: 717px;
	background: url('../img/map.jpeg') center (center/cover) no-repeat;
	padding: 49px 0;
	&_link {
		display: block;
		text-align: center;
		margin: 486px auto 0px auto;
		color: #1fbad6;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		text-decoration: none; } }

footer {
	min-height: 148px;
	padding: 14px 23px 31px 23px; }

.footer {
	&_line {
		width: 299px;
		height: 4px;
		background-color: $color_blue;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		padding-top: 32px;
		align-items: flex-start; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			width: 32px;
			height: 32px;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #ffffff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase;
				text-decoration: none; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				padding: 15px 0 19px 0;
				text-transform: uppercase;
				text-decoration: none; } }
		&_lang {
			display: flex;
			justify-content: center;
			color: $color_blue;
			font-size: 11px;
			font-weight: 400;
			text-align: center;
			text-transform: uppercase;
			text-decoration: none; } }
	&_mobile {
		img {
			width: 94px; } } }

.xs-visible {
    display: none; }
